<template>
  <el-container>
    <el-header class="qs-header">
      <el-row type="flex" justify="space-between">
        <div>
          <el-image style="margin: 12px;max-width: 180px;" src="logo.png" fit="cover"></el-image>
        </div>
        <div>
          <el-menu mode="horizontal" background-color="#5975ad" text-color="#fff" active-text-color="#fff">
            <el-submenu index="1">
              <template slot="title">快速导航</template>
              <el-menu-item @click="windowOpen(superUrls.mapleStory)">台服新枫之谷官网</el-menu-item>
              <el-menu-item @click="windowOpen(superUrls.hkBeanfun)">新香港橘子官网</el-menu-item>
              <el-menu-item @click="windowOpen(superUrls.twBeanfun)">台湾橘子官网</el-menu-item>
              <el-menu-item @click="windowOpen(superUrls.qstms)">秋水文献库</el-menu-item>
              <el-menu-item @click="windowOpen(superUrls.github)">秋水登录器官方Github</el-menu-item>
            </el-submenu>
            <el-menu-item :disabled="loginOutBtnStatus" @click="loginOut">退出登录</el-menu-item>
          </el-menu>
        </div>
      </el-row>
    </el-header>
    <el-container>
      <el-aside width="300px" style="margin-top: 30px;">
        <el-menu class="el-menu-vertical-demo">
          <el-menu-item @click="startGame(1)">
            <i class="el-icon-menu"></i>
            <span slot="title">启动游戏(32/64位)</span>
          </el-menu-item>
          <el-menu-item @click="startGame(2)">
            <i class="el-icon-menu"></i>
            <span slot="title">启动游戏(32/64位) 免密模式</span>
          </el-menu-item>
          <el-menu-item @click="autoLunShao">
            <i class="el-icon-menu"></i>
            <span slot="title">自动轮烧</span>
          </el-menu-item>
          <el-menu-item disabled>
            <i class="el-icon-setting"></i>
            <span slot="title">自动输入账号密码</span>
          </el-menu-item>
          <el-menu-item @click="open('memberCenter')">
            <i class="el-icon-setting"></i>
            <span slot="title">会员中心</span>
          </el-menu-item>
          <el-menu-item @click="open('memberTopUp')">
            <i class="el-icon-setting"></i>
            <span slot="title">充值中心</span>
          </el-menu-item>
          <el-menu-item @click="windowOpen('https://bfweb.hk.beanfun.com/newfaq/service_newBF.aspx')">
            <i class="el-icon-setting"></i>
            <span slot="title">客服中心</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main>
        <el-row type="flex">
          <el-col :span="10">
            <el-descriptions title="信息区" direction="vertical" :column="2" border>
              <template slot="extra">
                <el-button type="primary" v-if="!accounts || accounts.length === 0" @click="openOptAccount(1)">创建子账号
                </el-button>
                <el-button v-if="accounts && accounts.length != 0" @click="openOptAccount(2)">编辑子账号</el-button>
              </template>
              <el-descriptions-item>
                <template slot="label">
                  <el-select v-model="accountNow" @change="childrenAccountListSelect" placeholder="子账号列表">
                    <el-option v-for="item in accounts" :key="item.id" :label="item.name" :value="item"></el-option>
                  </el-select>
                  </el-form-item>
                </template>
                <span>{{ accountNow.name }}</span>
              </el-descriptions-item>
              <el-descriptions-item label="账号状态">
                <el-tag v-if="accountNow.status" type="success" size="small">健康</el-tag>
                <el-tag v-if="!accountNow.status" type="error" size="small">异常</el-tag>
              </el-descriptions-item>
              <el-descriptions-item>
                <template slot="label">
                  <el-button :disabled="updatePointsBtnStatus" size="small" @click="getPoints">更新点数</el-button>
                </template>
                <span>{{ accountPoints }}</span>
              </el-descriptions-item>
              <el-descriptions-item label="创建时间">
                <span>{{ accountNow.createTime | dateFormat }}</span>
              </el-descriptions-item>
              <el-descriptions-item label="游戏账号">
                <el-input v-clipboard:copy="dynamics.account" v-clipboard:success="copySuccess" type="text" readonly
                  v-model="dynamics.account"></el-input>
              </el-descriptions-item>
              <el-descriptions-item label="游戏动态密码(10分钟有效)">
                <template slot="label">
                  <el-button size="small" :disabled="dynamicPasswordBtnStatus" @click="getDynamicPassword">
                    获取游戏动态密码</el-button>
                </template>
                <el-input type="password" v-clipboard:copy="dynamics.password" v-clipboard:success="copySuccess"
                  readonly v-model="dynamics.password">
                </el-input>
              </el-descriptions-item>
              <el-descriptions-item :span="2">
                <template slot="label">
                  <el-button size="small" :disabled="fileGamePathBtnStatus" @click="openFileGamePath">设置游戏路径</el-button>
                </template>
                <el-input type="text" readonly :value="gamePath"></el-input>
              </el-descriptions-item>
            </el-descriptions>
          </el-col>
          <el-col :span="1">
          </el-col>
          <el-col :span="12">
            <el-descriptions title="功能区" direction="vertical" :column="3" border>

              <el-descriptions-item :span="3">
                <template slot="label">
                  <el-button size="small" @click="getRatesData()">汇率计算-更新汇率</el-button>
                </template>
                当前汇率
                <el-tag type="success" size="small">{{ rateModel.rate }}</el-tag>
                <span>更新时间:{{rateModel.time | dateFormat }}</span>
                </br>
                <label>软妹币
                  <el-input style="max-width: 200px;" @focus="rateModel.rmb = null"
                    @blur="!rateModel.rmb ? rateModel.rmb = 0 : rateModel.rmb = rateModel.rmb" @input="rateUpdateRmb"
                    type="text" v-model="rateModel.rmb"></el-input>
                </label>
                &lt;>
                <label>
                  <el-input style="max-width: 200px;" @focus="rateModel.twd = null"
                    @blur="!rateModel.twd ? rateModel.twd = 0 : rateModel.twd = rateModel.twd" @input="rateUpdateTwd"
                    type="text" v-model="rateModel.twd"></el-input>
                  新台币
                </label>
              </el-descriptions-item>
              <el-descriptions-item label="自动轮烧介绍">
                <div style="max-width: 400px;">
                  <p>自动轮并不被官方许可，使用过程中造成任何问题或风险自行承担后果，Java虚拟机模拟按键，绕过NGS检测机制。</p>
                </div>
              </el-descriptions-item>
              <el-descriptions-item label="键位设置">
                <label>轮回
                  <el-input style="max-width: 100px;" type="text" readonly @keyup.native="bindKeyLunhui"
                    v-model="lunshaoKeyForm.lunhui"></el-input>
                </label>
                </br>
                <label>燃烧
                  <el-input style="max-width: 100px;" type="text" readonly @keyup.native="bindKeyRanshao"
                    v-model="lunshaoKeyForm.ranshao"></el-input>
                </label>
              </el-descriptions-item>
              <el-descriptions-item label="启动状态">
                <span v-if="lunshaoStatus" style="color: green;">运行中</span>
                <span v-if="!lunshaoStatus" style="color: red;">停止</span>
              </el-descriptions-item>
            </el-descriptions>
          </el-col>
          <el-col :span="1">
          </el-col>
        </el-row>
      </el-main>
    </el-container>
    <el-footer class="qs-footer">
      <p class="footer-text">QsBeanfun v4.1</p>
    </el-footer>
  </el-container>
</template>
<script>
import apiBase from '@/api/ApiBase'
import apiUser from '@/api/ApiUser'
import config from '@/api/config'
import { dateDiffBetween } from '@/utils/DateUtils'
export default {
  data: () => ({
    rateModel: {
      rate: 0,
      time: 0,
      rmb: 0,
      twd: 0,
    },
    accounts: [],
    accountNow: {
      name: '',
      status: true,
      createTime: null,
    },
    dynamics: {
      account: '',
      password: '',
    },
    lunshaoKeyForm: {
      lunhui: 'b',
      ranshao: 'n',
    },
    gamePath: '',
    accountPoints: '',
    rateBtnStatus: false,
    dynamicPasswordBtnStatus: false,
    fileGamePathBtnStatus: false,
    updatePointsBtnStatus: false,
    loginOutBtnStatus: false,
    lunshaoStatus: false,
    lunshaoTypeBtn: 0,
    superUrls: {
      mapleStory: 'https://maplestory.beanfun.com/main',
      hkBeanfun: 'https://bfweb.hk.beanfun.com/',
      twBeanfun: 'https://tw.beanfun.com/',
      qstms: 'https://www.qstms.com/',
      github: 'https://github.com/starmcc/qs-beanfun',
    },
  }),
  created() {
    this.getGamePath()
    this.refreshAccounts()
    this.getPoints()
    this.getRatesData()
    this.readLunShaoData()
  },
  methods: {
    readLunShaoData() {
      apiBase.autoLunshao({ status: false, showTime: true }).then((res) => {
        if (res.code === 1) {
          this.lunshaoStatus = res.data !== 0
          this.lunshaoTypeBtn = res.data !== 0 ? 1 : 0
        }
      })
      config.getConfig('lunHuiKey', (key) => {
        this.lunshaoKeyForm.lunhui = String.fromCharCode(key)
      })
      config.getConfig('ranShaoKey', (key) => {
        this.lunshaoKeyForm.ranshao = String.fromCharCode(key)
      })
    },
    refreshAccounts() {
      apiUser.getAccountInfo().then((res) => {
        if (res.code === 1) {
          this.accounts = res.data
          if (!this.accounts || this.accounts.length < 1) {
            this.$message.error('获取账号信息失败!')
            return
          }
          this.accountNow = this.accounts[0]
          this.dynamics.account = this.accounts[0].id
        }
      })
    },
    getGamePath() {
      config.getConfig('gamePath', (path) => {
        this.gamePath = path
      })
    },
    getPoints() {
      this.updatePointsBtnStatus = true
      apiUser.getPoints().then((res) => {
        this.updatePointsBtnStatus = false
        if (res.code === 1) {
          this.accountPoints = res.data
        }
      })
    },
    loginOut() {
      this.loginOutBtnStatus = true
      apiUser
        .loginOut()
        .then((res) => {
          this.loginOutBtnStatus = false
          this.$router.push('/')
        })
        .catch(() => {
          this.loginOutBtnStatus = false
        })
    },
    copySuccess() {
      this.$message.success('复制成功')
    },
    open(code) {
      apiBase.webOpen({ code }).then((res) => {})
    },
    getRatesData() {
      this.rateBtnStatus = true
      apiBase
        .getRates()
        .then((res) => {
          this.rateBtnStatus = false
          if (res.code === 1) {
            this.rateModel.rate = res.data
            this.rateModel.time = res.responseTime
          }
        })
        .catch(() => {
          this.rateBtnStatus = false
        })
    },
    rateUpdateRmb(val) {
      if (!this.rateModel.rate) {
        return
      }
      this.rateModel.twd = Math.floor(val * this.rateModel.rate * 100) / 100
    },
    rateUpdateTwd(val) {
      if (!this.rateModel.rate) {
        return
      }
      this.rateModel.rmb = Math.floor((val / this.rateModel.rate) * 100) / 100
    },
    openFileGamePath() {
      this.fileGamePathBtnStatus = true
      apiBase
        .setGamePath()
        .then((res) => {
          this.getGamePath()
          this.fileGamePathBtnStatus = false
          if (res.code === 1) {
            this.$message.success(res.msg)
          } else {
            this.$message.warning(res.msg)
          }
        })
        .catch(() => {
          this.fileGamePathBtnStatus = false
        })
    },
    getDynamicPassword(fnc) {
      this.dynamicPasswordBtnStatus = true
      apiUser
        .getDynamicPassword()
        .then((res) => {
          this.dynamics.password = res.data
          this.dynamicPasswordBtnStatus = false
          if (res.code === 1) {
            this.$message.success('获取动态密码成功!')
            fnc()
          } else {
            this.$message.error(res.msg)
          }
        })
        .catch(() => {
          this.dynamicPasswordBtnStatus = false
        })
    },
    startGame(type) {
      if (type === 2) {
        this.getDynamicPassword(() => {
          apiBase
            .startGame({
              password: this.dynamics.password,
            })
            .then((res) => {
              if (res.code === 1) {
                this.$message.success(res.msg)
              } else {
                this.$message.error(res.msg)
              }
            })
        })
        return
      }

      apiBase
        .startGame({
          password: this.dynamics.password,
        })
        .then((res) => {
          if (res.code === 1) {
            this.$message.success(res.msg)
          } else {
            this.$message.error(res.msg)
          }
        })
    },
    bindKeyLunhui(key) {
      this.lunshaoKeyForm.lunhui = key.key.toUpperCase()
      config.setConfig('lunHuiKey', key.keyCode)
    },
    bindKeyRanshao(key) {
      this.lunshaoKeyForm.ranshao = key.key.toUpperCase()
      config.setConfig('ranShaoKey', key.keyCode)
    },
    autoLunShao() {
      this.lunshaoTypeBtn++
      let message = ''
      if (!this.lunshaoStatus) {
        message =
          '是否启动自动轮烧？<br>禁止商业用途<br>点击确定后，将在5秒后启动...<br>再次点击会停止,并显示使用时长!'
        this.$confirm(message, '自动轮烧', {
          dangerouslyUseHTMLString: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        })
          .then(() => {
            apiBase
              .autoLunshao({
                status: true,
                showTime: false,
              })
              .then((res) => {
                if (res.code === 1) {
                  this.lunshaoStatus = !this.lunshaoStatus
                  this.$message.success(res.msg)
                } else {
                  this.$message.error(res.msg)
                }
              })
          })
          .catch(() => this.lunshaoTypeBtn--)
      } else {
        if (this.lunshaoTypeBtn !== 2) {
          this.lunshaoTypeBtn = 1
          return
        }
        apiBase.autoLunshao({ status: false, showTime: true }).then((res) => {
          if (res.code === 1) {
            message =
              '立刻停止自动轮烧?<br>已经运行：' +
              dateDiffBetween(new Date().getTime() - res.data)
            this.$confirm(message, '自动轮烧', {
              dangerouslyUseHTMLString: true,
              confirmButtonText: '确定',
              cancelButtonText: '取消',
            })
              .then(() => {
                apiBase
                  .autoLunshao({
                    status: false,
                    showTime: false,
                  })
                  .then((res) => {
                    if (res.code === 1) {
                      this.lunshaoStatus = !this.lunshaoStatus
                      this.$message.success(res.msg)
                    } else {
                      this.$message.error(res.msg)
                    }
                  })
              })
              .catch(() => this.lunshaoTypeBtn--)
          }
        })
      }
    },
    childrenAccountListSelect(val) {
      // 请求
      apiUser.selectAccount({ accountId: this.val.id }).then((res) => {
        if (res.code === 1) {
          this.$message.success('已选择[' + val.name + ']子账号')
        }
      })
    },
    openOptAccount(type) {
      let prompt = this.$prompt(
        '请输入账号',
        type === 1 ? '创建账号' : '编辑账号',
        {
          closeOnClickModal: false,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputValue: this.accountNow ? this.accountNow.name : '',
        }
      )
      prompt.then(({ value }) => {
        let result
        if (type === 1) {
          result = apiUser.addAccount({ name: value })
        } else {
          result = apiUser.editAccount({ name: value })
        }
        result.then((res) => {
          if (res.code !== 1) {
            this.$message.error(res.msg)
            return
          }
          this.$message.success(res.msg)
          this.refreshAccounts()
        })
      })
    },
    windowOpen(url) {
      window.open(url)
    },
  },
}
</script>


<style scoped>
.qs-header {
  background-color: #5975ad;
  color: white;
}
.qs-footer {
  background-color: #5975ad;
  height: 30px !important;
  color: white;
  text-align: right;
  font-size: 15px;
}
.footer-text {
  margin: 5px;
}
</style>